.stars {
  display: flex;
  list-style: none;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;

    &:last-child {
      margin-right: 0;
    }
  }

  @for $i from 1 through 5 {
    &--#{$i} .stars__star:nth-child(-n + #{$i}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
